<template>
  <div class="page page-transaction_buy">
    <div class="top-wrapper">
      <div class="card">
        <div class="title">
          <span class="split"> </span>
          <span :class="status | transactingOrderStatus" class="status">
            {{ status | transactingOrderStatus("buyer-content") }}
          </span>
          <span
            :class="{ loading }"
            class="btn"
            @click="() => loadOrderDetail(orderInfo.orderId)"
          >
            <van-icon name="replay"/>
          </span>
        </div>
        <div class="time">
          <template v-if="status == TRANSACTING_ORDER_STATUS.BUYER_REQUEST">
            卖家
            <span style="color: red">
              {{ $root.waitingSellerConfirmOrderStr }}
            </span>
            内未确认订单，系统默认卖家取消交易
          </template>
          <template
            v-if="status == TRANSACTING_ORDER_STATUS.WAITING_FOR_TRANSFER"
          >
            买家
            <span style="color: red">
              {{ $root.buyerPayingTimeOverStr }}
            </span>
            内未操作,系统将默认买家取消交易
          </template>
          <template v-if="status == TRANSACTING_ORDER_STATUS.TIME_OVER">
            未操作超时取消，如有异议请在30分钟内提交
            <span style="color: #fb2222">[申请客服介入]</span>
            ，否则交易将自动结束，买家无法收到转币
          </template>
          <template v-if="status == TRANSACTING_ORDER_STATUS.TRANSFERRED">
            卖家确认付款后系统将自动将币转入您的账户
          </template>
          <template v-if="status == TRANSACTING_ORDER_STATUS.DISPUTING">
            争议订单将由官方客服介入协助处理
          </template>
          <template
            v-if="
              status == TRANSACTING_ORDER_STATUS.SELLER_DID_NOT_RECEIVE &&
              !isCountdownEnd
            "
          >
            卖家已提交
            <span style="color: #fb2222">[未收到付款]</span>
            ，如有异议请在30分钟内提交
            <span style="color: #6f59fe">[申请客服介入]</span>
            ，否则交易将自动结束买家无法收到转币
          </template>
          <template
            v-if="
              status == TRANSACTING_ORDER_STATUS.SELLER_DID_NOT_RECEIVE &&
              isCountdownEnd
            "
          >
            超时未提交异议，系统自动结束交易
          </template>
          <template v-else> &nbsp;</template>
        </div>
        <div class="orderId">
          订单编号 {{ orderInfo.orderNo }}
          <button
            v-clipboard="orderInfo.orderNo || ''"
            class="copy copyBtn"
            @success="$root.copySuccessful"
          />
        </div>
      </div>
    </div>
    <van-cell class="kv amount" title="购买数量">
      V币 {{ parseFloat(orderInfo.amount).toFixed(2) }}
    </van-cell>
    <van-cell class="kv payment" title="收款方式">
      <div
        v-if="PAYMENT_METHOD.BANK === orderInfo.paymentType"
        class="payment-icon bank"
      />
      <div
        v-if="PAYMENT_METHOD.ALI === orderInfo.paymentType"
        class="payment-icon ali"
      />
      <div
        v-if="PAYMENT_METHOD.WE_CHAT === orderInfo.paymentType"
        class="payment-icon wechat"
      />
    </van-cell>

    <!-- 步骤条 -->
    <van-cell-group :border="false" title="交易进度">
      <template #title>
        <span class="title">交易进度</span>
        <span
          v-if="
            status === TRANSACTING_ORDER_STATUS.BUYER_REQUEST ||
            status === TRANSACTING_ORDER_STATUS.WAITING_FOR_TRANSFER ||
            status === TRANSACTING_ORDER_STATUS.TIME_OVER ||
            (status === TRANSACTING_ORDER_STATUS.SELLER_DID_NOT_RECEIVE &&
              !isCountdownEnd)
          "
          class="countdown"
        >
          订单倒计时
          <van-count-down :time="countdown" @finish="onCountdownFinish"/>
        </span>
      </template>
      <van-steps :active="stepsActive">
        <van-step>
          {{ orderInfo.createTime | dateFormat("M/D") }}
          <div v-if="BUYER_STEP[status] >= 0" class="bottom-status time">
            {{ orderInfo.createTime | dateFormat("h:m:s") }}
          </div>
        </van-step>
        <van-step>
          卖家确认交易
          <div
            v-if="BUYER_STEP[status] >= 0"
            :class="status | transactingOrderStatus(undefined, 1)"
            class="bottom-status"
          >
            {{ status | transactingOrderStatus("step", 1) }}
          </div>
        </van-step>
        <van-step>
          买家转账
          <div
            v-if="BUYER_STEP[status] >= 2"
            :class="{
              ['time-over']: isTimeoutDisputing,
              [transactingOrderStatus(status, undefined, 2)]: true,
            }"
            class="bottom-status"
          >
            <template v-if="isTimeoutDisputing"> 超时取消</template>
            <template v-else>
              {{ status | transactingOrderStatus("step", 2) }}
            </template>
          </div>
        </van-step>
        <van-step>
          交易完成
          <div
            v-if="BUYER_STEP[status] >= 3"
            :class="status | transactingOrderStatus"
            class="bottom-status"
          >
            {{ status | transactingOrderStatus("step") }}
          </div>
        </van-step>
      </van-steps>
    </van-cell-group>
    <!-- 步骤条 -->

    <!-- 卖方收款信息 -->
    <components
      :is="componentsMapping[orderInfo.paymentType]"
      :hide="hide"
      :payment="hide ? undefined : sellerPayment"
      title="卖方收款信息"
    />

    <!-- 买方收款信息 -->
    <components
      :is="componentsMapping[orderInfo.paymentType]"
      :hide="hide"
      :payment="hide ? undefined : buyerPayment"
      title="买方付款信息"
    />

    <!-- 买方凭证 -->
    <template
      v-if="
        status == TRANSACTING_ORDER_STATUS.WAITING_FOR_TRANSFER ||
        status == TRANSACTING_ORDER_STATUS.TRANSFERRED ||
        (status == TRANSACTING_ORDER_STATUS.SELLER_DID_NOT_RECEIVE &&
          !isCountdownEnd) ||
        status == TRANSACTING_ORDER_STATUS.DISPUTING
      "
    >
      <van-image
        v-if="BUYER_STEP[status] > 2"
        :src="$root.getImageUrl(orderInfo.voucher)"
        class="certificationImage"
        fit="contain"
        @click="previewImage($root.getImageUrl(orderInfo.voucher))"
      >
      </van-image>

      <div v-else>
        <div
          v-if="imageList.length && imageList[0] && imageList[0].url"
          class="van-uploader fake"
        >
          <div class="van-uploader__wrapper">
            <div class="van-uploader__preview" @click="previewImage">
              <div class="van-image van-uploader__preview-image">
                <img
                  :src="imageList[0].url"
                  class="van-image__img"
                  style="object-fit: contain"
                />
              </div>
            </div>
            <div class="van-uploader__preview-delete" @click="imageList = []">
              <i
                class="
                  van-icon van-icon-cross
                  van-uploader__preview-delete-icon
                "
              ></i>
            </div>
          </div>
        </div>
        <van-uploader
          v-else
          v-model="imageList"
          :after-read="afterRead"
          :max-count="1"
          :preview-image="true"
          image-fit="contain"
          result-type="file"
          @click.native="(e) => onClickUpload(e)"
        >
          <van-button class="upload">
            <van-icon name="plus"></van-icon>
            <div class="text">请上传付款凭证</div>
          </van-button>
        </van-uploader>
      </div>
    </template>
    <!-- 买方凭证 -->

    <!-- 提示 -->
    <template v-if="status == TRANSACTING_ORDER_STATUS.WAITING_FOR_TRANSFER">
      <div class="warning">
        1.请务必使用您实名认证的付款方式进行转账，否则卖家有权利 延迟或拒绝转币
      </div>
      <div class="warning">
        2.转账成功后请上传真实付款凭证，对于提供虚假付款凭证用户
        平台将予以严肃处理，恶意提供虚假转账用户平台封号处理
      </div>
      <div class="warning">
        3.上传真实付款凭证后点击「我已转账」等待商家审核
      </div>
    </template>
    <!-- 提示 -->

    <div class="btns">
      <template
        v-if="
          status == TRANSACTING_ORDER_STATUS.TIME_OVER ||
          (status == TRANSACTING_ORDER_STATUS.SELLER_DID_NOT_RECEIVE &&
            !isCountdownEnd)
        "
      >
        <van-button class="primary block" @click="apply_disputing">
          申请客服介入
        </van-button>
      </template>
      <template v-if="status == TRANSACTING_ORDER_STATUS.WAITING_FOR_TRANSFER">
        <van-button class="danger block" @click="cancel_transaction">
          取消交易
        </van-button>
        <van-button class="primary block" @click="is_transferred">
          我已转账
        </van-button>
      </template>
      <template v-if="status == TRANSACTING_ORDER_STATUS.DISPUTING">
        <van-button class="primary block" @click="$root.openCustomerService">
          联系客服
        </van-button>
      </template>
    </div>

    <van-dialog
      v-model="isApplyDialogVisiblity"
      class="applyDialog"
      confirm-button-color="#6F59FE"
      confirm-button-text="提交申诉"
      show-cancel-button
      title="请简要描述您的申诉内容"
      @confirm="submitApplying"
    >
      <van-field
        v-model="applyContent"
        autocomplete="new-password"
        autosize
        maxlength="50"
        placeholder="0/50"
        rows="3"
        show-word-limit
        type="textarea"
      />
    </van-dialog>
  </div>
</template>

<script>
import {BUYER_STEP, PAYMENT_METHOD, STATUS_TYPE, TRANSACTING_ORDER_STATUS,} from "@/vendor/enum";
import {
  cancel_transaction,
  is_transferred,
  pls_upload_pay_certification,
} from "./components/dialogs";
import {AliPaymentInfo, BankPaymentInfo, WechatPaymentInfo,} from "../transaction_sale/components";
import {
  buyerAppeals,
  buyerCancelsOrder,
  buyerConfirmsOrderHasBeenPaid,
  getCancelsOrderDailyCount,
  getCurrentOrderStatus,
  myOrderDetail,
} from "@/apis/market";
import {upload} from "@/apis/common";
import {ImagePreview} from "vant";
import { cryptoMerchantMixins } from '@/vendor/mixins';
// 卖方单状态
/**
 * BUYER_REQUEST
 * SELLER_CANCELED_DEAL
 *
 * WAITING_FOR_TRANSFER
 * TIME_OVER
 *
 * TRANSFERRED
 * DISPUTING
 * SELLER_DID_NOT_RECEIVE
 * CANCELED_BY_OFFICIAL
 * CONFIRMED_BY_OFFICIAL
 *
 *
 */
export default {
  components: {BankPaymentInfo, WechatPaymentInfo, AliPaymentInfo},
  mixins: [cryptoMerchantMixins],
  data() {
    return {
      PAYMENT_METHOD,
      STATUS_TYPE,
      BUYER_STEP,
      TRANSACTING_ORDER_STATUS,
      status: TRANSACTING_ORDER_STATUS.WAITING_FOR_TRANSFER,
      componentsMapping: {
        [PAYMENT_METHOD.BANK]: "BankPaymentInfo",
        [PAYMENT_METHOD.ALI]: "AliPaymentInfo",
        [PAYMENT_METHOD.WE_CHAT]: "WechatPaymentInfo",
      },
      countdown: 0,
      isTimeOut: false,
      loading: false,
      isApplyDialogVisiblity: false,
      applyContent: "",
      orderInfo: {},
      sellerPayment: {},
      buyerPayment: {},
      imageList: [],
    };
  },
  computed: {
    hide() {
      // return (
      //   this.status == this.TRANSACTING_ORDER_STATUS.BUYER_REQUEST ||
      //   this.status == this.TRANSACTING_ORDER_STATUS.SELLER_CANCELED_DEAL ||
      //   this.status == this.TRANSACTING_ORDER_STATUS.BUYRER_CANCELD_DEAL ||
      //   (this.status == this.TRANSACTING_ORDER_STATUS.SELLER_DID_NOT_RECEIVE &&
      //     this.isCountdownEnd) || // 订单计时未结束
      //   this.status == this.TRANSACTING_ORDER_STATUS.CONFIRM_TRANSFER ||
      //   this.status == this.TRANSACTING_ORDER_STATUS.CANCELED_BY_OFFICIAL ||
      //   this.status == this.TRANSACTING_ORDER_STATUS.CONFIRMED_BY_OFFICIAL ||
      //   this.status == this.TRANSACTING_ORDER_STATUS.DEAL ||
      //   this.status == this.TRANSACTING_ORDER_STATUS.TIME_OVER
      // );
      return !(
        (this.status === this.TRANSACTING_ORDER_STATUS.TRANSFERRED &&
          !this.isCountdownEnd) ||
        this.status === this.TRANSACTING_ORDER_STATUS.DISPUTING ||
        this.status === this.TRANSACTING_ORDER_STATUS.WAITING_FOR_TRANSFER
      );
    },
    isCountdownEnd() {
      return this.countdown <= 0;
    },
    stepsActive() {
      return this.BUYER_STEP[this.status];
    },
    isTimeoutDisputing() {
      if (
        this.status == this.TRANSACTING_ORDER_STATUS.DISPUTING &&
        this.isTimeOut
      ) {
        return true;
      }
      return false;
    },
  },
  created() {
    this.load();
    this.$root.$emit("root:set-topbar", ["left-back", "customer-service"]);
  },
  beforeDestroy() {
    clearTimeout(this.timer);
  },
  methods: {
    transactingOrderStatus(...args) {
      return this.$options.filters.transactingOrderStatus(...args);
    },
    cancel_transaction() {
      getCancelsOrderDailyCount(undefined, this.BuyerApiPath).then(({ok, data, msg}) => {
        if (ok) {
          cancel_transaction(
            data.allowedCancelsOrderDailyCount - data.cancelsOrderDailyCount,
            data.allowedCancelsOrderDailyCount
          ).then(() => {
            buyerCancelsOrder({orderId: this.$route.params.id}, this.BuyerApiPath).then(
              ({ok, msg}) => {
                if (ok) {
                  this.status = TRANSACTING_ORDER_STATUS.BUYRER_CANCELD_DEAL;
                  this.$toast.success(msg);
                  this.load();
                } else {
                  this.$toast.fail(msg);
                }
              }
            );
          });
        } else {
          this.$toast.fail(msg);
        }
      });
    },
    is_transferred() {
      if (!this.imageList.length) {
        return pls_upload_pay_certification();
      }
      else if(!this.imageList[0].dataUrl) {
        return this.$toast.fail("无法获取到要上传的图片")
      }
      is_transferred().then(() => {
        buyerConfirmsOrderHasBeenPaid({
          orderId: this.$route.params.id,
          voucher: this.imageList[0].dataUrl,
        }, this.BuyerApiPath).then(({ok, msg}) => {
          if (ok) {
            this.status = TRANSACTING_ORDER_STATUS.TRANSFERRED;
            this.$toast.success(msg);
            this.load();
          } else {
            this.$toast.fail(msg);
          }
        });
      });
    },
    apply_disputing() {
      this.isApplyDialogVisiblity = true;
    },
    submitApplying() {
      buyerAppeals({
        orderId: this.$route.params.id,
        remark: this.applyContent.trim(),
      }, this.BuyerApiPath).then(({ok, msg}) => {
        if (ok) {
          this.applyContent = ''
          if (this.status === TRANSACTING_ORDER_STATUS.TIME_OVER) {
            this.isTimeoutDisputing = true;
          }
          this.status = TRANSACTING_ORDER_STATUS.DISPUTING;
          this.$toast.success(msg);
          this.load();
        } else {
          this.$toast.fail(msg);
        }
      });
    },
    load() {
      this.loadOrderDetail(this.$route.params.id);
    },
    loadOrderDetail(orderId) {
      if (this.loading) return;
      this.loading = true;
      myOrderDetail({orderId}, this.BuyerApiPath).then(({ok, msg, data}) => {
        this.loading = false;
        if (ok) {
          clearTimeout(this.timer);
          this.orderInfo = data.order;
          this.sellerPayment = data.sellerPayment;
          this.buyerPayment = data.buyerPayment;
          // 争议订单
          if (this.orderInfo.appeal > 0) {
            this.status = TRANSACTING_ORDER_STATUS.DISPUTING;
            if (this.orderInfo.status === TRANSACTING_ORDER_STATUS.TIME_OVER) {
              this.isTimeOut = true;
            }
          } else {
            this.status = this.orderInfo.status;
          }
          if (data.currentStateExpireTime) {
            if (data.currentStateExpireTime.expire) {
              this.countdown =
                data.currentStateExpireTime.expire * 1000 -
                this.$root.getServerTime();
            } else {
              this.countdown = 0;
            }
          }
          this.refreshStatus();
        } else {
          this.$toast.fail(msg);
        }
      });
    },
    refreshStatus() {
      this.timer = setTimeout(() => {
        getCurrentOrderStatus({orderId: this.orderInfo.orderId}, this.BuyerApiPath).then(
          ({ok, data}) => {
            this.refreshStatus();
            if (ok) {
              const old_status = this.status;
              // 争议订单
              if (this.orderInfo.appeal > 0) {
                this.status = TRANSACTING_ORDER_STATUS.DISPUTING;
                if (data.status === TRANSACTING_ORDER_STATUS.TIME_OVER) {
                  this.isTimeOut = true;
                }
              } else {
                this.status = data.status;
              }
              if (this.status !== old_status) {
                this.loadOrderDetail(this.orderInfo.orderId);
              }
            }
          }
        );
      }, 10 * 1000);
    },
    onCountdownFinish() {
      this.load();
    },
    afterRead(file) {
      this.$nextTick(() => {
        if(!this.imageList[0]) {
          if(file) {
            this.imageList[0] = file;
          } 
          else {
            Sentry.captureMessage("can not get the file at afterRead function")
            this.$toast.fail("无法获取到要上传的图片")
            return
          }
        }
        this.imageList[0].status = "uploading";
        this.imageList[0].message = "上传中...";
        upload({ 
          _file: file.file,
          imgFile: this.$root.isH5Plus ? file._filePath : file.file, 
          type: "tradeImg"
        }).then(
          ({ok, data, msg}) => {
            if (ok) {
              this.imageList[0].url = this.$root.getImageUrl(data.name);
              this.imageList[0].dataUrl = data.name;
              this.imageList[0].status = "done";
              this.$set(this.imageList, 0, this.imageList[0]);
            } else {
              this.imageList[0].status = "error";
              this.imageList[0].message = "上传失败";
              this.$set(this.imageList, 0, this.imageList[0]);
              this.$toast.fail(msg);
            }
          }
        );
      })
    },
    onClickUpload(e) {
      if (this.$root.isH5Plus) {
        e.preventDefault();
        window.h5p.uploadImage(async (blob, name, type, path) => {
          if (blob) {
            var fs = new File([blob], name, {
                type: type,
            });
            this.imageList = [{ file: fs, _filePath: path}];
            this.afterRead(this.imageList[0]);
          } else {
            this.$toast.fail("获取图片失败");
          }
        });
      }
    },
    previewImage(path) {
      if(path) {
        ImagePreview([path]);
      }
      else if(this.imageList[0]) {
        ImagePreview([this.imageList[0].url]);
      }
      else {
        Sentry.captureMessage("transaction_buy previewImage no valid url")
      }
    },
  },
};
</script>

