import {Dialog} from 'vant';

export const dialog_generator = (title, message) => new Promise((resolve, reject) => {
  Dialog.confirm({
    title,
    message,
    confirmButtonText: '是',
    cancelButtonText: '否',
    confirmButtonColor: '#6f59fe',
    allowHtml: true,
    beforeClose(action, done) {
      done();
      if (action === 'confirm') {
        resolve()
      } else {
        // reject()
      }
    }
  })
  .catch(_ => {})
})

export const cancel_transaction = (lease_cancel_time, maxium_cancelable_time = 3) => dialog_generator("你确定取消该笔交易",
  `<div>
      <div class="tip">
        提示：
      </div>
      <div>
        1.如果你已给卖方付款，请勿取消交易；
      </div>
      <div>
      2.如果你在一天内取消<span style="color:#FB2222">${maxium_cancelable_time}次</span>买卖订单，当天你的账户将会被停止买卖。
      </div>
      <div class="al-c">
          今日还可取消
          <span style="color:#FB2222">${lease_cancel_time}次</span>
          挂单，请谨慎操作
      </div>
    </div>
`)

export const is_transferred = () => dialog_generator("是否已转账", `请再次确认您已使用实名认证账户成功转账，
如发现有违规操作可能会面临
<span style="color:#FB2222">
    「永久封号」
</span>
处理`)

